html{
    height: 100%;
}
body {
    font-family: 'Montserrat', sans-serif;
    height: 100%;
}

.form-control {
    background-color: #ffffff;
    background-image: none;
    border: 1px solid #dcddde;
    border-radius: 0;
    box-shadow: 0 0px 0px rgba(0, 0, 0, 0.075) inset;
    color: #333333;
    display: block;
    font-size: 14px;
    height: 44px;
    line-height: 1.42857;
    padding: 6px 12px;
    transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
    width: 100%;
    font-weight: 400;
}

.login-box,
.register-box {
    width: 360px;
    margin:  auto;
}

.login-page,
.register-page {
    background: #d2d6de;
}

.login-logo,
.register-logo {
    font-size: 35px;
    text-align: center;
    margin-bottom: 25px;
    font-weight: 300;
}
.login-logo img{
    width: 95px;
    height: 95px;
    object-fit: contain;
    margin-top: 60px;
}
.login-box-msg,
.register-box-msg {
    margin: 0;
    text-align: center;
    padding: 0 20px 20px 20px;
}

.login-box-body,
.register-box-body {
    background: #fff;
    padding: 20px;
    border-top: 0;
    color: #666;
}

.has-feedback {
    position: relative;
}

.form-group {
    margin-bottom: 15px;
}

.has-feedback .form-control {
    padding-right: 42.5px;
}

.login-box-body .form-control-feedback,
.register-box-body .form-control-feedback {
    color: #777;
}

.form-control-feedback {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    display: block;
    width: 34px;
    height: 34px;
    line-height: 44px;
    text-align: center;
    pointer-events: none;
}

.btn {
    border-radius: 0px;
}

.btn-primary {
    background: #ffd304;
    border-color: #ceab04;
    transition: all .2s ease-in;
    &:hover {
        background: #254783;
    }
}
.centerBlock{
    background: #39c4e4;
    height: 100%;
}
