.collapse {
    .panel-default > .panel-heading {
        background-color: transparent;
        border-bottom: transparent;
        padding: 15px;
        color: #6bbedf;
    }
    .panel-group .panel {
        border-radius: none;
    }
    .panel {
        margin-bottom: 0px;
        background-color: transparent;
       border: none;
        border-radius: none;
        box-shadow: none;
        a {
            display: block;
            text-decoration: none;
            font-weight: 300;
             color: #6bbedf;
            font-size: 14px;
        }
    }
    .panel-heading:hover {
        background: rgba(255, 255, 255, 0.2) !important;
        color: white;
        text-decoration: none;
        a:focus,
        a:hover {
            text-decoration: none;
        }
    }
    .panel-heading.active {
         background: rgba(255, 255, 255, 0.2) !important;
        color: white;
    }

}
.panel-default>.panel-heading+.panel-collapse>.panel-body{
    border-top: 1px solid #405779;
}
.panel-default>.panel-heading+.panel-collapse>.role_panel_body{
    border-top: 1px solid #ddd ! important;
